import { IonButton, IonContent, IonFooter, IonHeader, IonImg, IonPage, IonText, IonTitle, IonToolbar, useIonViewDidEnter } from '@ionic/react';
import React, { useState } from 'react';
import { IonGrid, IonRow, IonCol } from '@ionic/react';
import './Home.css';
import ReactAudioPlayer from 'react-audio-player';
import { Device } from '@capacitor/device';

const soundList = [
  {
    name: 'train', 
    image: 'https://www.freesoundslibrary.com/wp-content/uploads/2019/12/cartoon-train-whistle.jpg',
    audio: 'https://webpage.pace.edu/mk69024n/cartoon-train-whistle.mp3'
  },
  {
    name: 'xyklophone', 
    image: 'https://www.freesoundslibrary.com/wp-content/uploads/2018/03/cartoon-xylophone-gliss.jpg',
    audio: 'https://webpage.pace.edu/mk69024n/cartoon-xylophone-gliss.mp3'
  },
  {
    name: 'dog', 
    image: 'https://www.freesoundslibrary.com/wp-content/uploads/2021/03/dog-squeaky-toy-sound.jpg',
    audio: 'https://webpage.pace.edu/mk69024n/dog-squeaky-toy-sound.mp3'
  },
  {
    name: 'birthday', 
    image: 'https://www.freesoundslibrary.com/wp-content/uploads/2020/08/funny-birthday-party-horn.jpg',
    audio: 'https://webpage.pace.edu/mk69024n/funny-birthday-party-horn.mp3'
  },
  {
    name: 'kazoo', 
    image: 'https://www.freesoundslibrary.com/wp-content/uploads/2020/12/party-kazoo-sound-effect.jpg',
    audio: 'https://webpage.pace.edu/mk69024n/party-kazoo-sound-effect.mp3'
  },
  {
    name: 'bell', 
    image: 'https://www.freesoundslibrary.com/wp-content/uploads/2019/05/small-bell-ringing-sound-effect.jpg',
    audio: 'https://webpage.pace.edu/mk69024n/small-bell-ringing-sound-effect.mp3'
  },

]
const Home: React.FC = () => {
  const [currentItem, setCurrentItem] = useState<any>({});
  const [batteryInfo, setBatteryInfo] = useState<any>(100);
  const onItemClick = (item: any) => {
    console.log(item);
    setCurrentItem(item);
  }
  const logBatteryInfo = async () => {
    const info = await Device.getBatteryInfo();
    setBatteryInfo(info.batteryLevel)
    console.log(info.batteryLevel);
  };
  useIonViewDidEnter(() => {
    console.log('ionViewDidEnter event fired');
    logBatteryInfo();
    
  });

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonTitle class="ion-text-center" color="shade" className="header"> <h1 >Click on image to play sound</h1></IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent fullscreen>
        <IonGrid className="body">
          <IonRow>
          {
            soundList.map((sound: any) =>
            <IonCol size="6" key={sound.name}>
              <IonImg className='image'
                onClick={e => onItemClick(sound)}
                src={sound.image} />
            </IonCol>
            )}
          </IonRow>           
        </IonGrid>
        

      </IonContent>
      <IonFooter>
        <IonToolbar>
        <ReactAudioPlayer
          src= {currentItem.audio}
          autoPlay
          controls
        />
        <div><IonText className="battery"><h2>Battery Level: {Math.round(batteryInfo*100)}</h2></IonText></div>
        </IonToolbar>
      </IonFooter>
    </IonPage>
  );
};

export default Home;

